// app/amenities/[slug]/page.tsx

import React from "react";
import Link from "next/link";
import { amenities } from "../../../lib/amenities";
// 1Ô∏è‚É£ Put the amenities array directly in this file


// 2Ô∏è‚É£ Page component
export default function AmenityDetail({
  params,
}: {
  params: { slug: string };
}) {
  // Find the amenity by slug
  const amenity = amenities.find((a) => a.slug === params.slug);

  if (!amenity) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold mb-4">Amenity Not Found</h1>
          <Link href="/amenities">
            <button className="px-6 py-2 bg-blue-600 text-white rounded-lg">
              Back to Amenities
            </button>
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto py-20 px-6">
      <h1 className="text-4xl font-bold flex items-center gap-4">
        <span className="text-5xl">{amenity.icon}</span>
        {amenity.name}
      </h1>

      <p className="mt-6 text-lg text-muted-foreground">
        {amenity.description}
      </p>

      <Link href="/amenities" className="mt-8 inline-block text-blue-600 hover:underline">
        ‚Üê Back to Amenities
      </Link>
    </div>
  );
}
